import React, { Component } from 'react'
import { NavLink, Route ,Switch} from 'react-router-dom'

import Header from '../../components/Header'
import Footer from '../../components/Footer'
import NavSection from '../../components/NavSection'
import WorkSection from '../WorkSection'
import BlogSection from '../BlogSection'
import GallerySection from '../GallerySection'
import AboutSection from '../AboutSection'

import './index.css'


export default class HomePage extends Component {
  render() {
    return (
      <div>
        <Header />
        <NavSection/>
        <main id="MainPart" className="mainpart">
          <div className="tabsection">
            <div data-duration-in="300" data-duration-out="100" className="tabs w-tabs">
              <div className="tabs-menu w-tab-menu" role="tablist">
                  <NavLink activeClassName="w--current" className="tablink w-inline-block w-tab-link" to={`/home/work`}>WORKS</NavLink>
                  <NavLink activeClassName="w--current" className="tablink w-inline-block w-tab-link" to={`/home/blog`}>BLOG</NavLink>
                  <NavLink activeClassName="w--current" className="tablink w-inline-block w-tab-link" to={`/home/gallery`}>GALLERY</NavLink>
                  <NavLink activeClassName="w--current" className="tablink w-inline-block w-tab-link" to={`/home/about`}>ABOUT</NavLink>
              </div>
              <div className="tabs-content w-tab-content">
                <div className="w-tab-pane w--tab-active">
                  
                  <Switch>
                    <Route path="/home/work" component={WorkSection} />
                    <Route path="/home/blog" component={BlogSection} />
                    <Route path="/home/gallery" component={GallerySection} />
                    <Route path="/home/about" component={AboutSection} />
                    </Switch>
                  

                  </div>
              </div>
            </div>
          </div>
          
        </main>
        <Footer />

      </div>
    )
  }
}
